.section__left {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;
}

.dropdown__elem__list {
  list-style: none;
  width: 95%;
}
.dropdown__elem__list .dropdown__con {
  padding: 10px;
  border-bottom: 1px solid rgb(20, 20, 20);
  margin-bottom: 5px;
}

.dropdown__title__con {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  height: min-content;
  width: 100%;
  padding: 1px 5px;
}

.elem__title {
  margin-block-start: 0.2em;
  margin-block-end: 0.5em;
}

.input-half {
  width: 45%;
}

.dropdown__content {
  padding: 5px;
}

.add__input__elem__con {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.add__input__elem {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  border: 1px solid rgb(20, 20, 20);
  border-radius: 3px;
  padding: 7px;
  margin: 10px;
  font-size: 17px;
  width: fit-content;
  transition: border 0.1s ease;
}
.add__input__elem:hover, .add__input__elem:focus {
  border: 1px solid rgb(76, 0, 197);
}
.add__input__elem .material-symbols-outlined {
  font-size: 18px;
}

.content__creator__con {
  border: 1px solid black;
  border-radius: 1em;
  padding: 15px;
  transition: border 0.1s ease;
}
.content__creator__con:hover, .content__creator__con:focus {
  border: 1px solid rgb(76, 0, 197);
}

input {
  width: 100%;
  border: 1px solid transparent;
  border-radius: 2px;
  background-color: rgb(224, 224, 224);
  padding: 7px;
  margin-bottom: 5px;
  font-size: 17px;
  transition: border 0.1s ease;
}
input:hover, input:focus {
  border: 1px solid rgb(76, 0, 197);
}

button {
  border: 1px solid rgb(20, 20, 20);
  background: transparent;
  padding: 7px;
  font-size: 17px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  transition: border 0.1s ease;
}
button:hover, button:focus {
  border: 1px solid rgb(76, 0, 197);
}